.custom-sliding-pane {
  width: 500px !important;
  height: calc(100% - 80px);
  margin-top: 80px;
}

.slide-pane__header {
  display: none;
}

.slide-pane__content {
  padding: 0;
}

.custom-overlay-class {
  background-color: transparent !important;
}

.react-tooltip-opacity {
  opacity: 1 !important;
  padding: 5px 10px !important; 
}

@media (max-width: 600px) {
  .custom-sliding-pane {
    width: 90% !important;
  }
}